import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import cookiesData from './cookiesData';

// Styled components
const DetailContainer = styled.div`
  padding: 20px;
`;

const Header = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-left: 5%;
  margin-bottom: 8%;
`;

const CookieTitle = styled.p`
  font-size: 130%;
  margin-bottom: 10%;
`;

const HeaderLeft = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 5%;
  justify-content: center;
`;

const HeaderRight = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

const CookieProperty = styled.div`
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
`;

const Badge = styled.img`
  padding: 0% 0%.5;
  transform: scale(0.8);
`;

const CookieImage = styled.img`
  width: 80%;
  border-radius: 10px;
`;

const DetailPageContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 10%;
`;

const SliderContainer = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 5%;
`;

const SliderLabel = styled.label`
  width: 15%;
`;
const StyledSlider = styled.input.attrs({ type: 'range' })`
  width: 80%;
  height: 10px;
  appearance: none;
  background: #B9B9B9;
  border-radius: 10px;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;

  &:hover {
    opacity: 1;
  }

  &::-webkit-slider-thumb {
    appearance: none;
    width: 20px;
    height: 20px;
    background: #D8D8D8;
    cursor: pointer;
    border-radius: 50%;
  }

  &::-moz-range-thumb {
    background: #D8D8D8;
    cursor: pointer;
    border-radius: 50%;
  }
`;

const StatsContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin: 5%;
  flex: 1;
`;

const DetailStatsContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`

const StatsValueContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex: 1;
`;

const Text = styled.text`
  font-size: 16px;
`;

const PotionContainer = styled.div`
  display: flex;
  gap: 5px;
  margin: 5%;
  align-items: center;
  justify-content: center;
`;

const PotionImage = styled.img`
  width: 50px;
  height: 50px;
`;

const PotionText = styled.p`
  align-items: center;
  justify-content: center;
  display: flex;
  margin-top: 1%;
`;

const Button = styled.button`
 font-family: CustomFont;
  padding: 9px;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
`;

const Icon = styled.img`
  margin-right: 10px;
`;


const CookieDetailPage = () => {
  const { cookieName } = useParams();
  const cookie = cookiesData.find((c) => c.path === cookieName);

  const [level, setLevel] = useState(1);
  const [levelData, setLevelData] = useState(null);

  useEffect(() => {
    fetch('https://sseong-mi.github.io/Tower-of-Adventures/levelsData.json')
      .then(response => response.json())
      .then(data => {
        const cookieLevelData = data[cookie.path];
        setLevelData(cookieLevelData);
      })
      .catch(error => console.error('Error fetching levels data:', error));
  }, [cookie]);

  const handleLevelChange = (e) => {
    setLevel(Number(e.target.value));
  };

  if (!cookie) {
    return <DetailContainer>쿠키 정보를 찾을 수 없습니다.</DetailContainer>;
  }

  if (!levelData) {
    return <DetailContainer>레벨 데이터를 불러오는 중입니다...</DetailContainer>;
  }

  const currentLevelData = levelData.find((l) => l.level === level);
  const { hp, attack, defense, potionsRequired } = currentLevelData;

  return (
    <DetailContainer>
      <Header>
        <HeaderLeft>
          <CookieTitle>{cookie.name}</CookieTitle>
          <CookieProperty>
            <Badge src={`https://sseong-mi.github.io/Tower-of-Adventures/images/attributes/${cookie.attribute}.png`} alt={cookie.attribute} />
            <Badge src={`https://sseong-mi.github.io/Tower-of-Adventures/images/types/${cookie.type}.png`} alt={cookie.type} />
            <Badge src={`https://sseong-mi.github.io/Tower-of-Adventures/images/grades/${cookie.grade}.png`} alt={cookie.grade} />
            <Badge src={`https://sseong-mi.github.io/Tower-of-Adventures/images/marks/${cookie.mark}.png`} alt={cookie.mark} />
          </CookieProperty>
          <DetailPageContainer>
            <Button>레벨업</Button>
            <Button>승급</Button>
            <Button>스킬</Button>
          </DetailPageContainer>
        </HeaderLeft>
        <HeaderRight>
          <CookieImage src={cookie.image} alt={cookie.name} />
        </HeaderRight>
      </Header>
      <StatsContainer>
        <SliderContainer>
          <SliderLabel>Lv.{level}</SliderLabel>
          <StyledSlider min="1" max="50" value={level} onChange={handleLevelChange} />
        </SliderContainer>
        <DetailStatsContainer>
          <Icon src='https://sseong-mi.github.io/Tower-of-Adventures/images/items/attack.png'></Icon>
          <StatsValueContainer>
            <Text>공격력</Text>
            <Text>{attack}</Text>
          </StatsValueContainer>
        </DetailStatsContainer>
        <DetailStatsContainer>
          <Icon src='https://sseong-mi.github.io/Tower-of-Adventures/images/items/defense.png'></Icon>
            <StatsValueContainer>
              <Text>방어력</Text>
              <Text>{defense}</Text>
            </StatsValueContainer>
        </DetailStatsContainer>
        <DetailStatsContainer>
          <Icon src='https://sseong-mi.github.io/Tower-of-Adventures/images/items/HP.png'></Icon>
          <StatsValueContainer>
            <Text>HP</Text>
            <Text>{hp}</Text>
          </StatsValueContainer>
        </DetailStatsContainer>
      </StatsContainer>
      <PotionContainer>
        {potionsRequired.map((potion, index) => (
          <div key={index}>
            <PotionImage 
              src={
                index === 3? `https://sseong-mi.github.io/Tower-of-Adventures/images/level_items/low_${cookie.attribute}.png`:
                index === 4? `https://sseong-mi.github.io/Tower-of-Adventures/images/level_items/mid_${cookie.attribute}.png`:
                index === 5? `https://sseong-mi.github.io/Tower-of-Adventures/images/level_items/high_${cookie.attribute}.png` :
                `https://sseong-mi.github.io/Tower-of-Adventures/images/level_items/${index}.png`} alt={`Potion ${index}`} />
            <PotionText>{potion}</PotionText>
          </div>
        ))}
      </PotionContainer>
    </DetailContainer>
  );
};

export default CookieDetailPage;